import React from "react"
import { Link, graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {kebabCase, toLower} from "lodash"
import 'prismjs/plugins/line-numbers/prism-line-numbers.css';

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO title="All posts" />
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          const tags = node.frontmatter.tags

          return (
            <div className="mt-4 mb-4 pt-4 pb-4" key={node.fields.slug}>
              <h4 className="mb-2">
                <Link style={{ boxShadow: `none`, textDecoration: `none`, color: `#17a2b8` }} to={node.fields.slug}>
                  {title}
                </Link>
              </h4>
              {/* <small>{node.frontmatter.date}</small> */}
              <div className="d-flex align-items-center mb-1">
                <div className="mr-2" style={{color: '#666666', fontSize: '13px'}}>{node.frontmatter.date}</div>
                {
                  (tags && tags.length > 0) &&
                  tags.map(tag => (
                    <Link
                        to={`/tags/${kebabCase(toLower(tag))}`}
                        className="badge badge-info pt-1 pb-1 pr-2 pl-2 ml-1"
                        key={tag}
                      >
                      <FontAwesomeIcon icon={'tag'} size="xs" className={"mr-1"} />
                      {tag}
                    </Link>
                  ))
                }
              </div>
              <p
                style={{color: '#999999'}}
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </div>
          )
        })}
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 10
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD HH:mm:ss")
            title
            tags
          }
        }
      }
    }
  }
`
